#ShikigamiAttrbute{
	position: fixed;
	left: 50%;
	top:50%;
	width: 40vw;
	height: 50vw;
	margin-left: -20vw;
	margin-top: -25vw;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	border: 1px #000000 solid;
	background-color: #ffffff;
}
#ShikigamiAttrbute div{
	-webkit-box-flex: 1;
	    -ms-flex: 1;
	        flex: 1;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-align:center;
	    -ms-flex-align:center;
	        align-items:center;
	-webkit-box-orient:vertical;
	-webkit-box-direction:normal;
	    -ms-flex-direction:column;
	        flex-direction:column;
}
#ShikigamiAttrbute .left{
	-webkit-box-flex: initial;
	    -ms-flex: initial;
	        flex: initial;
	width: 30%;
	background-color: #ffffff;
}
#ShikigamiAttrbute .left img{
	background-color: #8B6851;
	width: 10vw;
	height: 15vw;
	margin-top: 1vw;
}
#ShikigamiAttrbute .left span{
	margin-top: 0.5vw;
	font-size: 1.5vw;
}
#ShikigamiAttrbute .right{
	background-color: #ffffff;
}
#ShikigamiAttrbute .right div{
	width: 100%;
	-webkit-box-orient:horizontal;
	-webkit-box-direction:normal;
	    -ms-flex-direction:row;
	        flex-direction:row;
	-webkit-box-align:auto;
	    -ms-flex-align:auto;
	        align-items:auto;
	-webkit-box-flex: 1;
	    -ms-flex: 1;
	        flex: 1;
}
#ShikigamiAttrbute .right div span{
	width: 5vw;
}
#ShikigamiAttrbute .right div:last-of-type{
	-webkit-box-flex: initial;
	    -ms-flex: initial;
	        flex: initial;
	height: 15%;
}
#ShikigamiAttrbute .close,#ShikigamiAttrbute .save{
	-webkit-box-flex: initial;
	    -ms-flex: initial;
	        flex: initial;
	position: absolute;
}
#ShikigamiAttrbute .close{
	width: 3vw;
	height: 3vw;
	border-radius: 50%;
	background-color: #ffffff;
	right: -1.5vw;
	top: -1.5vw;
	border: 1px #000000 solid;
	line-height: 3vw;
}
#ShikigamiAttrbute .save{
	font-size: 2vw;
	color: #8b6851;
	bottom: 1vw;
	right: 1vw;
}
